@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@300;500;700;800&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.articles{width:70%;margin:auto;display:flex;flex-wrap:wrap;justify-content:center;margin-bottom:300px}.article{width:350px;height:300px;margin:20px;position:relative;box-shadow:5px 5px 10px 5px rgba(0,0,0,0.15),-5px -5px 10px 5px rgba(240,240,240,0.5);align-items:flex-end;font-size:8px}img{position:absolute;left:50%;top:50%;transform:translateX(-50%) translateY(-50%);z-index:1;max-height:100%;width:100%}.text-container{display:flex;align-items:center;min-height:72px}.article-title{position:absolute;z-index:2;background:white;width:90%;padding:5px;bottom:10px;margin-left:-15px;box-shadow:-5px -5px 10px 5px rgba(0,0,0,0.1)}

.App{font-family:'Manrope', sans-serif}

